import request from '@/utils/request'

// 获取所有主要类型列表
export function getMainTypes() {
  return request({
    url: '/openapi/v1.1/mainTypes',
    method: 'get'
  })
}
// 次要类型列表
export function getSecondTypes(mainType) {
  return request({
    url: mainType ? ('/openapi/v1.1/secondTypes/' + mainType) : '/openapi/v1.1/secondTypes',
    method: 'get'
  })
}
// 获取所有供应商列表
export function getSuppliers() {
  return request({
    url: '/openapi/v1.1/suppliers',
    method: 'get'
  })
}
// 分页查询产品列表
export function getProducts(params) {
  return request({
    url: '/openapi/v1.1/products',
    method: 'get',
    params
    // mainType pageNum pageSize productName secondType supplierName
  })
}



