<template lang='jade'>
	.container
		el-form(ref='searchForm', :model='searchForm')
			el-form-item
				el-select(v-model='searchForm.supplierName' filterable placeholder='供应商' clearable)
					el-option(v-for='(item, index) of supplierArray', :label='item.label', :value='item.value', :key='index')
			el-form-item
				el-input(type='text' v-model='searchForm.productCode' placeholder='产品代码' clearable)
			el-form-item
				el-input(type='text' v-model='searchForm.productName' placeholder='产品名称' clearable)
			el-form-item
				el-select(v-model='searchForm.mainType' placeholder='主要类型' @change='changeMain' @clear='clearMain' clearable)
					el-option(v-for='(item, index) of mainTypeArray', :label='item.label', :value='item.value', :key='index')
			el-form-item
				el-select(v-model='searchForm.secondType' placeholder='次要类型' clearable)
					el-option(v-for='(item, index) of secondTypeArray', :label='item.label', :value='item.value', :key='index')
			el-form-item(style='width:auto')
				el-button(type='primary' icon='el-icon-search' size='medium' @click='searchList' v-has='"home_search"') 查询
		.table
			el-table(:data='tableData' v-loading='loading' element-loading-text='拼命加载中' border)
				el-table-column(v-for='{prop, label, minWidth} in tableHead', :prop='prop', :label='label', :min-width='minWidth', :key='prop')
					template(slot-scope='scope')
						template {{scope.row[prop]}}
			el-pagination(@size-change='handleSizeChange' @current-change='handleCurrentChange', :current-page.sync='currentPage', :page-sizes='[10, 25, 50]', :page-size='currentSize' layout='sizes, prev, pager, next, total, jumper', :total='total')
</template>
<script>
	import { getSuppliers, getMainTypes, getSecondTypes, getProducts } from '@/api/creditProduction'
	export default {
		data() {
			return {
				supplierArray: [],
				mainTypeArray: [],
				secondTypeArray: [],
				currentPage: 1,
				currentSize: 10,
				loading: false,
				total: 0,
				searchForm: {
					supplierName: '',
					productCode: '',
					productName: '',
					mainType: '',
					secondType: '',
					pageNum: 1,
					pageSize: 10
				},
				tableHead: [
					{ prop: 'supplierNameCode', label: '供应商 (代码)', minWidth: '160px' },
					{ prop: 'productNameCode', label: '产品名称 (产品代码)', minWidth: '160px' },
					{ prop: 'necessaryParamters', label: '必需查询要件 (要件参数名)', minWidth: '200px' },
					{ prop: 'mainType', label: '主要类型', minWidth: '100px' },
					{ prop: 'secondaryType', label: '次要类型', minWidth: '100px' }
				],
				tableData: []
			}
		},
		methods: {
			getSuppliersData() {
				getSuppliers()
					.then((res) => {
						this.supplierArray = []
						for (let item of res.data) {
							this.supplierArray.push({ value: item.name, label: item.name + '(' + item.code +')'})
						}
					})
					.catch(() => {
						console.log('getSuppliers failed!!!')
					})
			},
			getMainTypesData() {
				getMainTypes()
					.then((res) => {
						this.mainTypeArray = []
						for (let item of res.data) {
							this.mainTypeArray.push({ value: item, label: item })
						}
					})
					.catch(() => {
						console.log('getMainTypes failed!!!')
					})
			},
			getSecondTypesData(mainType) {
				getSecondTypes(mainType)
					.then((res) => {
						this.secondTypeArray = []
						for (let item of res.data) {
							this.secondTypeArray.push({ value: item, label: item })
						}
					})
					.catch(() => {
						console.log('getSecondType failed!!!')
					})
			},
			getProductsList() {
				this.loading = true
				this.tableData = []
				this.searchForm.pageNum = this.currentPage
				this.searchForm.pageSize = this.currentSize
				getProducts(this.searchForm)
					.then((res) => {
						this.loading = false
						this.total = res.data.total
						this.tableData = res.data.list
					})
					.catch(() => {
						this.loading = false
						console.log('getProducts failed')
					})
			},
			searchList() {
				this.currentPage = 1
				this.getProductsList()
			},
			handleSizeChange(currentSize) {
				this.currentSize = currentSize
				this.getProductsList()
			},
			handleCurrentChange(currentPage) {
				this.currentPage = currentPage
				this.getProductsList()
			},
			changeMain(mainType) {
				this.getSecondTypesData(mainType)
				this.searchForm.secondType = ''
			},
			clearMain() {
				this.getSecondTypesData()
				this.searchForm.secondType = ''
			}
		},
		created() {
			this.getProductsList()
			this.getSuppliersData()
			this.getMainTypesData()
			this.getSecondTypesData()
		}
	}
</script>
<style lang='stylus' scoped>
.container
	.el-form
		background #fff
		padding 20px 15px 0 15px
		box-shadow 1px 1px 3px #dedede
		.el-form-item
			display inline-block
			width 220px
			margin-right 10px
			>>>.el-input__inner:focus
				border-color #67C23A
			.el-button--medium
				padding 11px 20px
	.table
		padding 20px 15px 1px 15px
		background #fff
		margin-top 20px
		box-shadow -1px -1px 3px #eee
		.el-table
			font-size 13px
			>>>td,
			>>>th
				color #666
				padding 8px 0
</style>


